<template>
    <div class="col-lg-12 mt-3 px-0">
        <div class="heading-border mb-2">
            <h5 class="h5">
                <span id="TabHeading2">Online Purchase</span>
                <small class="float-right">
                    <a href="javascript:;" class="addPurchaseLinkRows" title="" @click="showRow">
                        <i class="fa fa-plus pr-1"></i>Add Row
                    </a>
                </small>
            </h5>
        </div>
        <Form ref="AddOnlineForm">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-3 " v-show="isVisible">
                <div class="row addOnlinePurchaseRows" v-for="(item, id) in newDynamicField" :key="id.count">
                    <div class="col-lg-2">

                        <div class="form-group">
                            <label>Vendor:<span class="text-danger">*</span></label>
                            <div class="input-group">
                                <Field :name="'Vendor' + (index + 1)" class="w-100" :rules="{ 'required': true }"
                                    v-slot="{ errors }">
                                    <select name="Vendor" v-model="item.purchase_vendor"
                                        v-bind:class="{ 'Vendor form-control w-100': true, 'is-invalid': errors[0] }">
                                        <option value="">Select</option>
                                        <option v-for="item in venderList" :value="item.Value" :key="item">
                                            {{ item.Text }}
                                        </option>
                                    </select>

                                    <!-- <span v-if="errors[0]" :name="'Vendor' + (index + 1)" class="invalid-feedback d-block">{{
                            errors[0] }}</span> -->
                                    <input type="hidden" class="hdnpurchase_id" v-model="item.purchase_id" value="0">
                                </Field>
                            </div>

                        </div>
                    </div>
                    <div class="input-group-append" style="cursor:pointer;">
                        <span class="input-group-text border-0 bg-transparent p-0 pl-2"><a @click="Addvendor"
                                class="round-icon-small btn-dark theme-primary round-icon-small AddQuoteVendor"><i
                                    class="fa fa-plus pt-7 AddQuoteVendor " id="AddQuoteVendor" title="Add Vendor"
                                    onclick="AddVendor('quote');"></i></a></span>
                    </div>

                    <div class="col-lg-2">
                        <div class="form-group">
                            <label>Requested Quantity:<span class="text-danger">*</span></label>
                            <div class="input-group">

                                <Field name="Request Quantity" :rules="{ 'required': true }" v-slot="{ errors }">
                                    <input v-model="item.txtpurchaseQuantity" type="number" min="1"
                                        v-on:keyup="calculateTotalQuantity(item)"
                                        v-bind:class="{ 'RequestQuantity form-control': true, 'is-invalid': errors[0] }" />
                                    <!-- <span v-if="errors[0]" name="Request Quantity" class="invalid-feedback d-block">{{
                                    errors[0] }}</span> -->
                                </Field>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-1 ">
                        <div class="form-group">
                            <label>Currency:<span class="text-danger">*</span></label>
                            <div class="input-group">

                                <Field name="Currency" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                                    <select name="Currency" v-model="item.getCurrencyValue"
                                        v-bind:class="{ 'Currency form-control w-100': true, 'is-invalid': errors[0] }">
                                        <option value="">Select</option>

                                        <option v-for="(item, currencyIndex) in currencyList" :value="item.CurrencyId"
                                            :key="currencyIndex">
                                            {{ item.Currency }}
                                        </option>
                                    </select>
                                    <!-- <span v-if="errors[0]" name="Currency" class="invalid-feedback d-block">{{
                                    errors[0] }}</span> -->
                                </Field>

                            </div>

                        </div>
                    </div>
                    <div class="col-lg-1 pl-0">
                        <div class="form-group">
                            <label>Unit Price:<span class="text-danger">*</span></label>

                            <Field name="Unit Price" :rules="{ 'required': true }" v-slot="{ errors }">
                                <input v-model="item.priceAmount" type="number" min="1"
                                    v-on:keyup="calculateTotalQuantity(item)"
                                    v-bind:class="{ 'UnitPrice form-control': true, 'is-invalid': errors[0] }" />
                                <!-- <span v-if="errors[0]" name="Unit Price" class="invalid-feedback d-block">{{
                                errors[0] }}</span> -->
                            </Field>


                        </div>
                    </div>
                    <div class="col-lg-1 pl-0">
                        <div class="form-group">
                            <label>Total Price:<span class="text-danger">*</span></label>
                            <input v-model="item.purchasePrice" type="text" disabled
                                class="totalPrice form-control TotalPrice" />

                        </div>
                    </div>
                    <div class="purchaseDynamicField col-lg-3 " v-for="(purchase, index) in item.purchaseDynamicField"
                        :key="'A' + index">

                        <div class="form-group">
                            <!-- <label>Purchase Link<span class="text-danger">*</span></label> -->
                            <label>Purchase Link<span class="text-danger"></span></label>


                            <div class="input-group">
                                <input type="text" ref="pasteInput" @paste="handlePaste($event, item, purchase)"
                                    @keyup="purchaseUrlData(item, purchase)" v-model="purchase.purchase_link"
                                    class="purchaseLink form-control" name="purchaseLink">
                                <input type="hidden" class="purchase_link_id" v-model="purchase.purchase_link_id">
                                <div class="input-group-append" style="cursor:pointer;">
                                    <span class="group-span-filestyle input-group-btn input-group-text bg-grey">
                                        <!-- Add the "copyToClipboard" method to the @click event -->
                                        <a title="Click here to copy link."
                                            @click="copyToClipboard(purchase.purchase_link)"
                                            class="mb-0 font-weight-normal "><i class="fa fa-copy"></i></a>
                                    </span>

                                    <span class="input-group-text border-0 bg-transparent p-0 pl-2">
                                        <a href="javascript:void(0);" @click="addLinkRow(item.count)"
                                            v-if="0 == parseInt(index)"
                                            class="round-icon-small btn-dark theme-primary round-icon-small">
                                            <i class="fa fa-plus pt-7"></i>
                                        </a>
                                        &nbsp;

                                        <!-- Add the "removeRow" method to the @click event -->
                                        <a href="javascript:void(0);" @click="removeRow(item, purchase);"
                                            class="btn btn-danger theme-primary removefield">
                                            <i class="fa fa-times pt-7" title=""></i>
                                        </a>
                                    </span>
                                </div>
                            </div>

                        </div>
                    </div>

                </div>

            </div>
        </Form>
        <AddVendorPopUp v-if="isShowvendor"></AddVendorPopUp>
    </div>
</template>
<script>
import DataService from "../../service/DataService";
import AddVendorPopUp from '../../components/Modal/AddVendorModel.vue'

export default {
    components: {
        AddVendorPopUp
    },
    props: {
        venderList: {
            type: Array
        },
        currencyList: {
            type: Array
        }
    },

    data() {
        return {
            isShowvendor: false,
            isVisible: false,
            newDynamicField: [{
                purchase_id: 0,
                purchase_vendor: '',
                txtpurchaseQuantity: '',
                getCurrencyValue: '',
                purchasePrice: '',
                priceAmount: '',
                count: 1,
                purchaseDynamicField: [
                    {
                        purchase_link_id: 0,
                        purchase_link: '',
                        pcount: 1
                    }
                ],
            }

            ],

            specificCount: null,
            purchaseData: {},
            pushaseEditFieldsData: [],
            purchaseEditField: [],
            Pushlink: [],
            copyText: '',
            pasteText: ''
        }
    },

    async created() {
        var vm = this;
        if (this.$route.params.reqForm == 'ProductTypeList') {
            this.productItem = this.$route.params.items;
            if (this.productItem != null && this.productItem != '' && this.productItem != 'undefined') {
                await vm.UpdateRequisition(this.productItem.AssetRequisitionId);
            }
        }

    },

    // mounted() {
    //     this.$refs.pasteInput.addEventListener('paste', (event) => {
    //         event.preventDefault();
    //         const text = event.clipboardData.getData('text');
    //         this.pasteText = text;
    //     });
    // },

    methods: {

        copyToClipboard(purchaseLink) {
            navigator.clipboard.writeText(purchaseLink)
                .then(() => {
                    console.log('Text copied to clipboard');
                })
                .catch((error) => {
                    console.error('Unable to copy text: ', error);
                });
        },

        handlePaste(event, item, purchases) {
            const text = event.clipboardData.getData('text');
            // this.pasteText = text;
            let purchase = {
                pcount: purchases.pcount,
                purchase_link_id: purchases.purchase_link_id,
                purchase_link: text
            }
            this.purchaseUrlData(item, purchase)

        },


        UpdateRequisition: async function (id) {
            var vm = this;
            vm.isLoading = true;
            try {
                const response = await DataService.UpdateRequisition(`id=${id}`);
                if (response.data !== null) {
                    vm.newDynamicField = [];
                    vm.purchaseEditField = [];
                    vm.purchaseEditFieldsData = JSON.parse(response.data.PurchaseNewRequisitionList);
                    vm.purchaseEditFieldsData.forEach((element, index) => {
                        element.count = index + 1;
                        var linksArray = JSON.parse(element.links);
                        let purchaseEditFieldArray = [];
                        linksArray.forEach((link, indx) => {

                            let obj = {
                                purchase_link_id: link.purchaseLinkId,
                                purchase_link: link.purchaseLink,
                                pcount: indx + 1
                            };
                            purchaseEditFieldArray.push(obj);
                        });
                        element.purchaseDynamicField = purchaseEditFieldArray;
                    });

                    vm.purchaseEditFieldsData.forEach(value => {
                        var obj = {
                            purchase_id: 0,
                            purchase_vendor: '',
                            txtpurchaseQuantity: '',
                            getCurrencyValue: '',
                            purchasePrice: '',
                            priceAmount: '',
                            purchaseDynamicField: [],
                        };
                        obj.purchase_id = value.purchaseId;
                        obj.purchase_vendor = value.vendorId;
                        obj.txtpurchaseQuantity = value.quantity;
                        obj.getCurrencyValue = value.currencyId;
                        obj.purchasePrice = value.totalPrice;
                        obj.priceAmount = value.price;
                        obj.purchaseDynamicField = value.purchaseDynamicField;
                        obj.count = value.count;
                        vm.newDynamicField.push(obj);
                        value.purchaseDynamicField.forEach(ele => {

                            vm.Pushlink.push({
                                purchase_link_id: ele.purchase_link_id,
                                purchase_link: ele.purchase_link,
                            });
                        })
                        let pushObj = {
                            purchase_id: value.purchaseId,
                            purchase_vendor: value.vendorId,
                            purchaseQuantity: value.quantity,
                            purchasePrice: value.currencyId,
                            ppriceAmount: value.totalPrice,
                            purchaseTotalPrice: value.totalPrice,
                            purchaselinks: vm.Pushlink
                        };
                        vm.$emit('purchases', pushObj);
                        vm.isVisible = true;
                    });
                    vm.isLoading = false;
                }
            } catch (error) {
                console.error("Error in UpdateRequisition:", error);
            } finally {
                vm.isLoading = false;
            }
        },



        Addvendor() {
            this.isShowvendor = true;
        },

        CloseAddVendorModel() {
            var vm = this;
            vm.isShowvendor = false;
            vm.$emit('refershVendorlist', 'refresh');
        },


        calculateTotalQuantity(item) {

            var vm = this;
            vm.specificCount = item.count;

            vm.newDynamicField.forEach(element => {

                if (element.count == vm.specificCount) {
                    element.purchasePrice = item.txtpurchaseQuantity * item.priceAmount;
                }

            })

            const getRecordInformation = vm.newDynamicField.find(x => x.count === vm.specificCount);
            if (getRecordInformation != null) {

                let pushLinks = [];

                getRecordInformation.purchaseDynamicField.map(ele => {
                    let obj = {
                        purchase_link_id: ele.purchase_link_id,
                        purchase_link: ele.purchase_link,
                    };
                    pushLinks.push(obj);
                })

                vm.purchaseData = {
                    purchase_id: getRecordInformation.purchase_id ? getRecordInformation.purchase_id : 0,
                    purchase_vendor: getRecordInformation.purchase_vendor,
                    purchaseQuantity: getRecordInformation.txtpurchaseQuantity,
                    purchasePrice: getRecordInformation.getCurrencyValue,
                    ppriceAmount: getRecordInformation.priceAmount,
                    purchaseTotalPrice: getRecordInformation.purchasePrice,
                    purchaselinks: pushLinks
                }

                vm.$emit('purchases', vm.purchaseData);
            }

        },

        purchaseUrlData(item, purchase) {
            var vm = this;
            const getRecordInformation = vm.newDynamicField.find(x => x.count === item.count);
            if (getRecordInformation != null) {

                let pushLinks = [];
                let obj = {}
                getRecordInformation.purchaseDynamicField.map(ele => {

                    if (ele.pcount == purchase.pcount) {
                        obj = {
                            purchase_link_id: ele.purchase_link_id,
                            purchase_link: purchase.purchase_link,
                        };
                    }
                    else {
                        obj = {
                            purchase_link_id: ele.purchase_link_id,
                            purchase_link: ele.purchase_link,
                        };
                    }
                    pushLinks.push(obj);

                })


                vm.purchaseData = {
                    purchase_id: getRecordInformation.purchase_id ? getRecordInformation.purchase_id : 0,
                    purchase_vendor: getRecordInformation.purchase_vendor,
                    purchaseQuantity: getRecordInformation.txtpurchaseQuantity,
                    purchasePrice: getRecordInformation.getCurrencyValue,
                    ppriceAmount: getRecordInformation.priceAmount,
                    purchaseTotalPrice: getRecordInformation.purchasePrice,
                    purchaselinks: pushLinks
                }

                vm.$emit('purchases', vm.purchaseData);
            }

        },


        showRow() {

            var vm = this;
            vm.$refs.AddOnlineForm.validate().then(result => {

                if (vm.isVisible == false) {
                    return vm.isVisible = true;
                }
                if (result) {

                    let singleDynamicRow = {
                        purchase_vendor: '',
                        txtpurchaseQuantity: '',
                        getCurrencyValue: '',
                        purchasePrice: '',
                        priceAmount: '',
                        count: 1,
                        purchaseDynamicField: [
                            {
                                purchase_link_id: 0,
                                purchase_link: '',
                                pcount: 1
                            }
                        ],
                    }

                    singleDynamicRow.count = vm.newDynamicField.length + 1;
                    vm.newDynamicField.push(singleDynamicRow);

                } else {
                    vm.ShowAlert(vm.$t("Please correct the highlighted errors"), "failure", vm.$t('Alert'));

                }

            })


        },

        addLinkRow(count) {
            var vm = this;
            vm.specificCount = count;
            vm.isVisible = true;
            let purchase = {};

            const getRecordInformation = vm.newDynamicField.find(x => x.count === vm.specificCount);
            if (getRecordInformation != null) {

                purchase = {
                    purchase_link_id: 0,
                    purchase_link: '',
                    pcount: vm.newDynamicField[0].purchaseDynamicField.length + 1
                }

                vm.newDynamicField.forEach(ele => {

                    if (ele.count == vm.specificCount) {
                        ele.purchaseDynamicField.push(purchase);
                    }
                })
            }


            let pushLinks = [];

            getRecordInformation.purchaseDynamicField.map(ele => {
                let obj = {
                    purchase_link_id: ele.purchase_link_id,
                    purchase_link: ele.purchase_link,
                };
                pushLinks.push(obj);
            })

            vm.purchaseData = {
                purchase_id: 0,
                purchase_vendor: getRecordInformation.purchase_vendor,
                purchaseQuantity: getRecordInformation.txtpurchaseQuantity,
                purchasePrice: getRecordInformation.getCurrencyValue,
                ppriceAmount: getRecordInformation.priceAmount,
                purchaseTotalPrice: getRecordInformation.purchasePrice,
                purchaselinks: pushLinks
            }

            vm.$emit('purchases', vm.purchaseData);
        },


        removeRow(item, purchase) {
            var vm = this;
            let newDynamicArrayCount = item.count;

            vm.newDynamicField.forEach((element) => item.purchaseDynamicField.forEach((subItem, index) => {

                if (element.count == newDynamicArrayCount) {

                    if (element.purchaseDynamicField.length === 1) {
                        const index = vm.newDynamicField.findIndex(x => x.count === newDynamicArrayCount);
                        if (index !== -1) {
                            vm.newDynamicField.splice(index, 1);
                            vm.$emit('purchaseDeleteItem', item)
                            return;

                        }
                    }
                    else if (subItem.pcount === purchase.pcount) {
                        element.purchaseDynamicField.splice(index, 1);

                        vm.$emit('purchaseLinkDeleteItem', element)
                        return;
                    }


                }
                else {
                    //
                }

            }));

        }
    }

}


</script>
<style scoped>
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type="number"] {
    -moz-appearance: textfield;
}
</style>