<template>
    <section class="main-content">
        <div class="theme-primary partition" id="NewRequisitionid">
            <span class="p-name text-white">
                {{ $t("NewProductTypeRequest") }}
            </span>
            <span class="p-actions float-right">
                <a title="Page Tour" class="p-action-btn text-white" v-on:click="setTourGuideSteps(0)">
                         <em class="fa fa fa-road"></em> <br><span>Page Tour</span>
                    </a>
                <a class="p-action-btn text-white" href="/AssetRequisition/NewAssetRequisitionListing"
                    data-toggle-tooltip="tooltip" data-original-title="Back to list"><i
                        class="fa fa-angle-double-left pr-2"></i><br><span>{{ $t("Backtolist") }}</span></a>
            </span>
        </div>
        <div class="border p-3">
            <Form ref="AddReqForm">
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 px-0">

                    <div class="row">
                        <div class="col-lg-3 ">
                            <div class="form-group">
                                <label>{{ $t('Location') }}<span class="text-danger">*</span></label>
                                <Field name="Location" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                                    <tree-view v-model="LocationValue" :options="options" placeholder="Select Location"
                                        :show-count="true" v-bind:class="{ 'is-invalid': errors[0] }">
                                    </tree-view>
                                    <span v-if="errors[0]" name="Location" class="invalid-feedback d-block">{{
                    errors[0]
                }}</span>
                                </Field>
                            </div>
                        </div>
                        <div class="col-lg-3">
                            <div class="form-group">
                                <label>{{ $t("ProductName") }}<span class="text-danger">*</span></label>
                                <Field name="Product Name" :rules="{ 'required': true }" v-slot="{ errors }">
                                    <input v-model="txtproductName" maxlength="100" type="text"
                                        v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }" />
                                    <span v-if="errors[0]" name="Product Name" class="invalid-feedback d-block">{{
                    errors[0] }}</span>
                                </Field>
                            </div>
                        </div>
                        <div class="col-lg-3">
                            <div class="form-group">
                                <label>{{ $t("ProductType") }}</label>
                                <Field name="AssetTypeId" class="w-100">
                                    <select name="AssetTypeId" class="form-control" v-model="selectedProductValue">
                                        <option value="">{{ $t('Select') }}</option>
                                        <option :value="item.ASSETID" v-for="item in AssetType" :key="item">
                                            {{ item.ASSETTYPE }} </option>
                                    </select>
                                </Field>
                            </div>
                        </div>
                        <div class="col-lg-3">
                            <div class="form-group">
                                <label>{{ $t("RequestedQuantity") }}<span class="text-danger">*</span></label>
                                <Field name="Quantity" :rules="{ 'required': true }" v-slot="{ errors }">
                                    <input v-model="txtrequestQuantity" type="number" min="1" maxlength="8"
                                        v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }" />
                                    <span v-if="errors[0]" name="Quantity" class="invalid-feedback d-block">{{
                    errors[0] }}</span>
                                </Field>
                            </div>
                        </div>
                    </div>

                </div>
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 px-0">
                    <div class="form-group">
                        <label for="Description">{{ $t('Description') }}<span class="text-danger">*</span></label>
                        <Field name="description" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                            <textarea name="description" type="text" v-model="txtDescription" class="form-control"
                                placeholder="Description" :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                rows="5" maxlength="250"></textarea>
                            <span v-if="errors[0]" name="description" class="invalid-feedback d-block">{{
                    errors[0]
                }}</span>
                        </Field>
                    </div>
                </div>

                <AddQuotes @quotes="quotes" @quoteItemDelete="quoteItemDelete" :venderList="venderList"
                    :currencyList="currencyList" @refershVendorlist="refershVendorlist"></AddQuotes>

            </Form>


            <OnlinePurchase @purchases="purchases" @purchaseDeleteItem="purchaseDeleteItem" :venderList="venderList"
                @refershVendorlist="refershVendorlist" :currencyList="currencyList"
                @purchaseLinkDeleteItem="purchaseLinkDeleteItem"></OnlinePurchase>


            <div class="col-lg-12 mb-3">
                <div class="row flex-row-reverse">
                    <div class="col-lg-6 text-right">
                        <a href="javascript:;" @click="saveNewRequisition(values, errors, 'ACT')"
                            class="btn btn-success  pr-2 mr-2"><i class="fa fa-save pr-2"></i>{{ $t("Save") }}</a>
                        <a href="javascript:;" @click="saveNewRequisitionAsDraft('Draft')"
                            class="btn btn-success  pr-2 mr-2"><i class="fa fa-save pr-2 "></i>
                            Save As Draft </a>
                        <a class="btn btn-danger mr-2" href="/AssetRequisition/NewAssetRequisitionListing"
                            data-original-title="Cancel"><i class="fa fa-close pr-2"></i>{{ $t("Cancel")
                            }}</a>
                    </div>
                    <div class="col-lg-6 py-2">
                        <span class="mandatory">{{ $t("Required") }}</span>
                    </div>
                </div>
            </div>

        </div>
    </section>
</template>

<script>
import AddQuotes from './AddQuotes.vue';
import OnlinePurchase from './OnlinePurchase.vue'
import Treeselect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
import DataService from '../../service/DataService';

export default {
    components: {
        AddQuotes,
        OnlinePurchase,
        'tree-view': Treeselect
    },
    data() {
        return {
            options: [],
            LocationValue: null,
            txtDescription: '',
            txtrequestQuantity: null,
            txtproductName: '',
            AssetType: [],
            selectedProductValue: null,
            isLoading: false,
            qoutesArray: [],
            purchaseArray: [],
            productItem: '',
            updateItemArray: [],
            updateRequistionId: 0,
            venderList: [],
            currencyList: []
        }
    },
    async created() {
        var vm = this;

        await vm.GetLocationByCompanyId();
        await vm.GetAssetType();
        await vm.getVendorlist();
        await vm.getCurrencylist();

        if (this.$route.params.reqForm == 'ProductTypeList') {
            this.productItem = this.$route.params.items;
            if (this.productItem != null && this.productItem != '' && this.productItem != 'undefined') {
                vm.txtrequestQuantity = this.productItem.Quantity;
                vm.txtproductName = this.productItem.ProductName;
                vm.txtDescription = this.productItem.Description;
                vm.selectedProductValue = this.productItem.AssetTypeId;
                vm.updateRequistionId = this.productItem.AssetRequisitionId;
                await vm.UpdateRequisition(this.productItem.AssetRequisitionId);

            }
        }
    },

    methods: {


        refershVendorlist(refresh) {
            this.getVendorlist();
        },

        async getVendorlist() {

            await DataService.GetCrmVendor().then((response) => {

                if (response.data != null) {
                    this.venderList = response.data;
                }
            })

        },

        async getCurrencylist() {

            await DataService.GetCurrency().then((response) => {
                if (response.data != null) {
                    this.currencyList = response.data;
                }
            })
        },


        UpdateRequisition: async function (id) {
            var vm = this;
            vm.isLoading = true;
            var params = `id=${id}`;
            await DataService.UpdateRequisition(params).then(response => {
                if (response.data != null) {
                    vm.updateItemArray = response.data;
                    vm.LocationValue = vm.updateItemArray.location_id;
                }
                vm.isLoading = false;
            });
        },
        GetLocationByCompanyId: async function () {
            var vm = this;
            var url = `reqForm=assetassignment`
            await DataService.GetLocationByuserId(url).then(response => {
                vm.FindalLocationArray = [];
                response.data.data.forEach((value) => {
                    var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                        return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                    });
                    if (CheckIfExist.length == 0) {
                        if (value.PARENT_LOCATION_ID == null) {
                            let locationId = value.COMPANY_LOCATION_ID
                            let rslt = vm.getChildLocation(locationId, value, response.data.data)
                            vm.FindalLocationArray.push(rslt)
                        }
                    }
                });
                vm.options = vm.FindalLocationArray;
            });
        },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },

        async GetAssetType() {
            var vm = this;
            vm.isLoading = true;
            var url = `type=ITEMCODE&issueTo=&locationId=`;
            await DataService.GetAssetTypeCustom(url).then(response => {
                vm.AssetType = JSON.parse(response.data);
                vm.isLoading = false;
            })

        },


        quoteItemDelete: function (quoteItemDelete) {
            debugger;
            var vm = this;
            const index = vm.qoutesArray.findIndex(x => x.quoteVendor == quoteItemDelete.getVenderValue);
            if (index !== -1) {
                vm.qoutesArray.splice(index, 1);
            }


        },


        quotes: function (quotes) {
            debugger;

            var vm = this;
            if (vm.qoutesArray.length == 0) {
                vm.qoutesArray.push(quotes);
                return;
            }
            else {
                // debugger;
                const index = vm.qoutesArray.findIndex(x => x.quoteVendor == quotes.quoteVendor);
                if (index !== -1) {
                    vm.qoutesArray.splice(index, 1);
                }
                vm.qoutesArray.push(quotes);
                return;
            }
        },


        purchaseLinkDeleteItem(item) {

            var vm = this;
            const getRecordInformation = vm.purchaseArray.find(x => x.purchase_vendor == item.purchase_vendor);
            if (getRecordInformation != null) {

                let pushLinks = [];

                item.purchaseDynamicField.map(ele => {
                    let obj = {
                        purchase_link_id: ele.purchase_link_id,
                        purchase_link: ele.purchase_link,
                    };
                    pushLinks.push(obj);
                })

                let obj = {
                    purchase_id: getRecordInformation.purchase_id ? getRecordInformation.purchase_id : 0,
                    purchase_vendor: item.purchase_vendor,
                    purchaseQuantity: item.txtpurchaseQuantity,
                    purchasePrice: item.getCurrencyValue,
                    ppriceAmount: item.priceAmount,
                    purchaseTotalPrice: item.purchasePrice,
                    purchaselinks: pushLinks
                }


                const index = vm.purchaseArray.findIndex(x => x.purchase_vendor == item.purchase_vendor);
                if (index !== -1) {
                    vm.purchaseArray.splice(index, 1);
                }
                vm.purchaseArray.push(obj);

            }

            console.log("vm.purchaseArray", vm.purchaseArray);

        },


        purchaseDeleteItem(purchases) {
            debugger;
            var vm = this;
            const index = vm.purchaseArray.findIndex(x => x.purchase_vendor == purchases.purchase_vendor);
            if (index !== -1) {
                vm.purchaseArray.splice(index, 1);
            }

        },



        purchases: function (purchases) {
            var vm = this;
            if (vm.purchaseArray.length == 0) {
                vm.purchaseArray.push(purchases);
            }
            else {
                const index = vm.purchaseArray.findIndex(x => x.purchase_vendor == purchases.purchase_vendor);
                if (index !== -1) {
                    vm.purchaseArray.splice(index, 1);
                }
                vm.purchaseArray.push(purchases);
                return
            }
        },


        saveNewRequisition(values, errors, ACT) {
            debugger;
            let status = ACT;
            var vm = this;

            vm.isLoading = true;

            var formData = new FormData();
            vm.$refs.AddReqForm.validate().then(result => {
                if (result) {


                    // var qoutesArrayList = [];
                    // var purchaseArrayList = [];

                    // $('.addQuoteRows').each(function () {
                    //     //debugger
                    //     var obj = {
                    //         quoteId: $(this).find('.hdnquoteId').val() == '' ? 0 : $(this).find('.hdnquoteId').val(),
                    //         quoteVendor: $(this).find('.Vendor').val(),
                    //         quoteQuantity: $(this).find('.RequestQuantity').val(),
                    //         quoteAmount: $(this).find('.Currency').val(),
                    //         quotePrice: $(this).find('.UnitPrice').val(),
                    //         quoteTotalPrice: $(this).find('.totalPrice').val(),
                    //         uploadQuote: '',
                    //         fileName: ''
                    //     }
                    //     qoutesArrayList.push(obj);
                    // })


                    // $('.addOnlinePurchaseRows').each(function () {
                    //     // debugger
                    //     var obj = {
                    //         purchase_id: $(this).find('.hdnpurchase_id').val() == '' ? 0 : $(this).find('.hdnpurchase_id').val(),
                    //         purchase_vendor: $(this).find('.Vendor').val(),
                    //         purchaseQuantity: $(this).find('.RequestQuantity').val(),
                    //         purchasePrice: $(this).find('.Currency').val(),
                    //         ppriceAmount: $(this).find('.UnitPrice').val(),
                    //         purchaseTotalPrice: $(this).find('.totalPrice').val(),
                    //         purchaselinks: []
                    //     }
                    //     $('.addOnlinePurchaseRows').find('.purchaseDynamicField').each(function () {

                    //         var modl = { purchase_link_id: $(this).find('.purchase_link_id').val() == '' ? 0 : $(this).find('.purchase_link_id').val(), purchase_link: $(this).find('.purchaseLink').val() };

                    //         obj.purchaselinks.push(modl);

                    //     })
                    //     purchaseArrayList.push(obj);
                    // })

                    let obj = {
                        RequisitionId: (vm.updateRequistionId ? vm.updateRequistionId : 0),
                        location: vm.LocationValue,
                        productname: vm.txtproductName,
                        productType: vm.selectedProductValue,
                        quantity: vm.txtrequestQuantity,
                        description: vm.txtDescription,
                        status: status,
                        quotes: vm.qoutesArray,
                        purchases: vm.purchaseArray,
                    }

                    formData.append("postString", JSON.stringify(obj));
                    DataService.AddUpdateNewRequisition(formData).then(response => {
                        if (response.data.msg == 'add') {
                            vm.isLoading = false;

                            vm.ShowAlert(vm.$t("New Product type Request successfully Added"), "Success", vm.$t('Alert'));
                            return vm.$router.push({ path: '/AssetRequisition/NewAssetRequisitionListing' });
                        }
                        else if (response.data.msg == 'update') {
                            vm.isLoading = false;

                            vm.ShowAlert(vm.$t("New Product type Request successfully Updated"), "Success", vm.$t('Alert'));
                            return vm.$router.push({ path: '/AssetRequisition/NewAssetRequisitionListing' });
                        }
                        else {
                            vm.isLoading = false;

                            return vm.ShowAlert(vm.$t("New Product type Request failed"), "failure", vm.$t('Alert'));
                        }
                    })
                }
                else {
                    vm.ShowAlert(vm.$t("Please correct the highlighted errors"), "failure", vm.$t('Alert'));

                }
            });

        },

        saveNewRequisitionAsDraft(event) {
            let status = event;
            var vm = this;
            vm.isLoading = true
            var formData = new FormData();
            vm.$refs.AddReqForm.validate().then(result => {
                if (result) {

                    // var qoutesArrayList = [];
                    // var purchaseArrayList = [];

                    // $('.addQuoteRows').each(function () {
                    //     //debugger
                    //     var obj = {
                    //         quoteId: $(this).find('.hdnquoteId').val() == '' ? 0 : $(this).find('.hdnquoteId').val(),
                    //         quoteVendor: $(this).find('.Vendor').val(),
                    //         quoteQuantity: $(this).find('.RequestQuantity').val(),
                    //         quoteAmount: $(this).find('.Currency').val(),
                    //         quotePrice: $(this).find('.UnitPrice').val(),
                    //         quoteTotalPrice: $(this).find('.totalPrice').val(),
                    //         uploadQuote: '',
                    //         fileName: ''
                    //     }
                    //     qoutesArrayList.push(obj);
                    // })


                    // $('.addOnlinePurchaseRows').each(function () {
                    //     //debugger
                    //     var obj = {
                    //         purchase_id: $(this).find('.hdnpurchase_id').val() == '' ? 0 : $(this).find('.hdnpurchase_id').val(),
                    //         purchase_vendor: $(this).find('.Vendor').val(),
                    //         purchaseQuantity: $(this).find('.RequestQuantity').val(),
                    //         purchasePrice: $(this).find('.Currency').val(),
                    //         ppriceAmount: $(this).find('.UnitPrice').val(),
                    //         purchaseTotalPrice: $(this).find('.totalPrice').val(),
                    //         purchaselinks: []
                    //     }
                    //     $('.addOnlinePurchaseRows').find('.purchaseDynamicField').each(function () {

                    //         var modl = { purchase_link_id: $(this).find('.purchase_link_id').val() == '' ? 0 : $(this).find('.purchase_link_id').val(), purchase_link: $(this).find('.purchaseLink').val() };

                    //         obj.purchaselinks.push(modl);

                    //     })
                    //     purchaseArrayList.push(obj);
                    // })


                    let obj = {
                        RequisitionId: (vm.updateRequistionId ? vm.updateRequistionId : 0),
                        location: vm.LocationValue,
                        productname: vm.txtproductName,
                        productType: vm.selectedProductValue,
                        quantity: vm.txtrequestQuantity,
                        description: vm.txtDescription,
                        status: status,
                        purchases: vm.purchaseArray,
                        quotes: vm.qoutesArray
                    }

                    formData.append("postString", JSON.stringify(obj));

                    DataService.AddUpdateNewRequisition(formData).then(response => {

                        if (response.data.msg == 'add') {
                            vm.isLoading = false;

                            vm.ShowAlert(vm.$t("New Product type Request Added as draft "), "Success", vm.$t('Alert'));
                            return vm.$router.push({ path: '/AssetRequisition/NewAssetRequisitionListing' });
                        }
                        else if (response.data.msg == 'update') {
                            vm.isLoading = false;

                            vm.ShowAlert(vm.$t("New Product type Request updated as draft "), "Success", vm.$t('Alert'));
                            return vm.$router.push({ path: '/AssetRequisition/NewAssetRequisitionListing' });
                        }
                        else {

                            vm.isLoading = false;
                            return vm.ShowAlert(vm.$t("New Product type Request failed"), "failure", vm.$t('Alert'));
                        }
                    })

                }
            })

        }

    }

}
</script>
<style scoped>
.vue-treeselect__control {
    border: none;
    height: 24px;
    padding: 0;
}

.vue-treeselect--searchable .vue-treeselect__input-container {
    padding: 0 !important;
}

.vue-treeselect__placeholder {
    color: #303030 !important;
}

.vue-treeselect__control-arrow {
    color: #000;
}

.vue-treeselect__placeholder {
    line-height: 24px;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type="number"] {
    -moz-appearance: textfield;
}
</style>


